import React, {
  useRef,
  useState,
  useEffect,
  Fragment,
  useCallback,
} from "react";
import Ttext from "../../../components/common/Ttext";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import * as actions from "../../../store/actions";
import {
  Box,
  Typography,
  Tooltip,
  IconButton,
  Paper,
  Button,
} from "@material-ui/core";
import SquareButton from "../../../components/Buttons/SquareButton";
import { tableIcons } from "../../../util/tableIcons";
import MaterialTable from "material-table";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import DeleteRoundedIcon from "@material-ui/icons/DeleteRounded";
import i18n from "../../../i18n";
import IconTextNotice from "../../../components/common/IconTextNotice";
import IosSwitch from "../../../components/Inputs/IosSwitch";
import { arSA } from "date-fns/locale";
import TableSkeleton from "../../../components/common/TableSkeleton";
import GeneralDialog from "../../../components/common/GeneralDialog";
import AssignmentIndRoundedIcon from "@material-ui/icons/AssignmentIndRounded";
import LockOpenRoundedIcon from "@material-ui/icons/LockOpenRounded";
import ButtonSpinner from "../../../components/UI/Spinners/ButtonSpinner";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="name" />,
    field: "name",
  },
  {
    title: <Ttext ns="common" text="email" />,
    field: "email",
  },
  {
    title: <Ttext ns="common" text="mobile" />,
    field: "mobile",
    render: (row) => (
      <Typography className="ltr" variant="body2">
        {row.mobile}
      </Typography>
    ),
  },
  {
    title: <Ttext ns="settings" text="role" />,
    field: "role",
    render: (row) => (
      <Typography className="ltr" variant="body2">
        <Ttext ns="settings" text={row.role} />
      </Typography>
    ),
  },
];

let timeout;

const Admins = ({ location }) => {
  const [deleteDialog, setDeleteDialog] = useState({
    open: false,
    selected: null,
  });

  const admins = useSelector((state) => state.admins);
  const [mounted, setMounted] = useState(false);
  const [resetLoad, setResetLoad] = useState(null);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();

  const fetchAdmins = useCallback(async () => {
    try {
      await dispatch(actions.getAdmins());
    } finally {
      setMounted(true);
    }
  }, [dispatch]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchAdmins();
    }, 0);
    return () => clearTimeout(timeout);
  }, [fetchAdmins]);

  const handleSwitchState = async (data) => {
    const id = data.admin_id;
    const status = data.status > 0 ? 0 : 1;
    try {
      await dispatch(actions.switchAdminStatus(id, status));
    } catch (err) {
      dispatch(actions.fireSnake("warning", err.message));
    }
  };

  const handleResetPassword = async (admin) => {
    try {
      setResetLoad(admin.admin_id);
      await dispatch(actions.adminPasswordReset(admin.admin_id));
      dispatch(
        actions.fireSnake(
          "success",
          t("settings:pass_reset_succes", { email: admin.email })
        )
      );
    } catch (err) {
      dispatch(actions.fireSnake("warning", err.message));
    }
  };

  const handleTriggerDeleteDialog = (selected) => {
    setDeleteDialog({
      open: !deleteDialog.open,
      selected: selected,
    });
  };

  const handleDeleteAdmin = async () => {
    const id = deleteDialog.selected.admin_id;
    try {
      await dispatch(actions.deleteAdmin(id));
      handleTriggerDeleteDialog();
    } catch (err) {
      dispatch(actions.fireSnake("warning", err.message));
    }
  };

  return (
    <Fragment>
      <Typography variant="h4" className="py">
        {t("settings:admins_list")}
      </Typography>
      <Box className="flex flex-justify-end py">
        <Link to={`${location.pathname}/add`}>
          <SquareButton
            title={t("settings:new_admin")}
            wIcon={<AssignmentIndRoundedIcon className="text-light" />}
            py={0.6}
            px={0.8}
            type="primary"
          />
        </Link>
      </Box>
      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} />
        </Paper>
      ) : (
        // : !admins.loading && admins.errors ? (
        //   <PageError
        //     text={admins.errors.message || t("common:uncommon_err")}
        //     onTryAgain={fetchAdmins}
        //   />
        // )
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={admins.admins}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const admin = props.data;
              return (
                <>
                  <Box className="center-self" style={{ width: 190 }}>
                    <Button
                      variant="outlined"
                      color="primary"
                      size="small"
                      onClick={() => handleResetPassword(admin)}
                      endIcon={
                        admins.itemLoading &&
                        resetLoad === admin.admin_id && <ButtonSpinner />
                      }
                      startIcon={<LockOpenRoundedIcon />}
                      disabled={admins.itemLoading}
                      style={{ width: "100%" }}
                    >
                      {t("settings:password_reset")}
                    </Button>
                  </Box>
                  <Tooltip title={t("common:edit")}>
                    <Link to={`${location.pathname}/edit/${admin.admin_id}`}>
                      <IconButton>
                        <EditRoundedIcon color="secondary" />
                      </IconButton>
                    </Link>
                  </Tooltip>
                  <Tooltip title={t("common:delete")}>
                    <IconButton
                      onClick={() => handleTriggerDeleteDialog(admin)}
                    >
                      <DeleteRoundedIcon color="error" />
                    </IconButton>
                  </Tooltip>
                  <IosSwitch
                    state={admin.status}
                    onChange={() => handleSwitchState(admin)}
                    name="status"
                    loading={admins.itemLoading}
                  />
                </>
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: admins.admins.length,
            pageSizeOptions: [admins.admins.length],
            thirdSortClick: false,
            showTitle: false,
            sorting: false,
          }}
          totalCount={admins.admins.length}
          page={0}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: admins.loading
                ? t("common:loadingggg")
                : !admins.loading &&
                  !admins.errors && (
                    <IconTextNotice
                      Icon={AssignmentIndRoundedIcon}
                      text={t("settings:no_admins")}
                    />
                  ),
              dateTimePickerLocalization: i18n.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={admins.loading}
        />
      )}
      <GeneralDialog
        dialogOpen={deleteDialog.open}
        confirmText={t("common:confirm")}
        title={t("common:confirm_delete")}
        onCancel={() => handleTriggerDeleteDialog()}
        onConfirm={handleDeleteAdmin}
        loading={admins.itemLoading}
        disableBackdropClick
      />
    </Fragment>
  );
};

export default Admins;
